@import url(https://fonts.googleapis.com/css2?family=Mitr:wght@300&display=swap);
.brand{border-width:0px;border-style:solid;font-size:2rem;border-radius:3rem;padding:1.5rem 3rem;margin:2rem auto}

.colorPicker{border-radius:50%;height:48px;width:48px;display:flex;align-items:center;justify-content:center;margin:.5rem 1rem}.colorPicker input[type="color"]{-webkit-appearance:none;padding:0;margin:0;border-radius:50%;border:0;height:32px;width:32px}.colorPicker input[type="color"]:focus,.colorPicker input[type="color"]:hover{outline:0}.colorPicker input[type="color"]::-webkit-color-swatch-wrapper{-webkit-appearance:none;cursor:pointer;border-radius:50%;padding:0}.colorPicker input[type="color"]::-webkit-color-swatch{border-radius:50%;box-shadow:var(--boxShadow)}

.container{margin:1rem auto;padding:1.2rem 2.4rem;border-radius:32px;border-width:1px}.container .code{font-family:monospace}

.radio{display:flex;border-radius:20px;margin:1em 0;padding:0.6rem;border-radius:50px;min-height:40px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.radio .option{cursor:pointer;flex:1 1;display:flex;justify-content:center;align-items:center;position:relative;padding:0.6rem 1.2rem;margin:0 .2rem;border-radius:20px;min-width:30px}.radio .option::after{position:absolute;content:'';height:1px;bottom:0}.radio .option input[type='radio']{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.radio .option .activeIndicator{height:0;position:absolute;bottom:1px;border-width:1px;border-style:solid}

.rangeInput{position:relative;padding:.5rem 1.2rem}.rangeInput::before{content:var(--val);position:absolute;right:-1rem;top:0;height:100%;transform:translateY(25%)}.rangeInput input[type="range"]{-webkit-appearance:none;width:100%;border-radius:16px;height:24px;padding:0 .5rem}.rangeInput input[type="range"]:focus,.rangeInput input[type="range"]:hover{outline:0}.rangeInput input[type="range"]:focus::-webkit-slider-thumb,.rangeInput input[type="range"]:hover::-webkit-slider-thumb{height:14px;width:14px}.rangeInput input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none;cursor:pointer;height:12px;width:12px;border-radius:50%;background:var(--bg);box-shadow:var(--boxShadow)}

.shadowGenerator{min-height:100%;width:100vw;display:flex;flex-direction:column;align-items:center;justify-content:center;box-sizing:border-box;padding:1rem}.shadowGenerator .field{display:flex;align-items:center}.shadowGenerator .field label{margin-right:1rem}

html,body{margin:0;padding:0;display:flex;height:100%;-webkit-tap-highlight-color:transparent}.row{display:flex;align-items:center;justify-content:space-between}

